'use client'

import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import ContactHero from "@/components/contact/hero"

const siteName = process.env.NEXT_PUBLIC_SITE_NAME || "Stampi"

export default function ContactPage() {
    return (
        <main>

            {/* ---------- HERO ---------- */}
            <ContactHero/>

            {/* ---------- CONTENT ---------- */}
            <section className="bg-light-shade/60 py-20">
                <div className="max-w-6xl mx-auto px-6 grid md:grid-cols-2 gap-10">

                    {/* LEFT: INFO */}
                    <div>
                        <h2 className="text-2xl font-semibold mb-4 text-primary">
                            Let’s Talk
                        </h2>
                        <p className="text-gray-600 mb-8 leading-relaxed">
                            Whether you’re a shopkeeper exploring loyalty programs or
                            already using {siteName}, feel free to reach out.
                            We usually respond within 24 hours.
                        </p>

                        <div className="space-y-4 text-sm">
                            <div>
                                <p className="font-medium text-gray-800">Email</p>
                                <p className="text-gray-600">support@stampi.in</p>
                            </div>

                            <div>
                                <p className="font-medium text-gray-800">Business Hours</p>
                                <p className="text-gray-600">Mon – Sat, 10:00 AM – 7:00 PM</p>
                            </div>

                            <div>
                                <p className="font-medium text-gray-800">Location</p>
                                <p className="text-gray-600">India</p>
                            </div>
                        </div>
                    </div>

                    {/* RIGHT: FORM */}
                    <Card className="border-0 shadow-sm rounded-2xl p-8">
                        <h3 className="text-xl font-semibold mb-6">
                            Send us a message
                        </h3>

                        <form className="space-y-5">
                            <div>
                                <label className="block text-sm font-medium mb-1">
                                    Your Name
                                </label>
                                <input
                                    type="text"
                                    placeholder="Enter your name"
                                    className="w-full rounded-lg border border-gray-200 px-4 py-2 focus:outline-none focus:ring-2 focus:ring-primary/30"
                                />
                            </div>

                            <div>
                                <label className="block text-sm font-medium mb-1">
                                    Email Address
                                </label>
                                <input
                                    type="email"
                                    placeholder="you@example.com"
                                    className="w-full rounded-lg border border-gray-200 px-4 py-2 focus:outline-none focus:ring-2 focus:ring-primary/30"
                                />
                            </div>

                            <div>
                                <label className="block text-sm font-medium mb-1">
                                    Message
                                </label>
                                <textarea
                                    rows="4"
                                    placeholder="Tell us how we can help you"
                                    className="w-full rounded-lg border border-gray-200 px-4 py-2 focus:outline-none focus:ring-2 focus:ring-primary/30"
                                />
                            </div>

                            <Button className="w-full h-11">
                                Send Message
                            </Button>
                        </form>
                    </Card>

                </div>
            </section>

            {/* ---------- CTA ---------- */}
            <section className="py-20">
                <div className="max-w-4xl mx-auto px-6 text-center">
                    <h2 className="text-3xl font-semibold mb-4">
                        Want to Get Started Faster?
                    </h2>
                    <p className="text-gray-600 mb-8">
                        Create your free account and start rewarding customers today.
                    </p>
                    <Button asChild size="lg">
                        <Link href="/signup">Start Free Trial</Link>
                    </Button>
                </div>
            </section>

        </main>
    )
}
